@import url(https://fonts.googleapis.com/css?family=Poppins);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.navbarContainer {
  background-color: #271e52;
  text-align: center;
  color: aliceblue;
  height: 10vh;
  position: fixed;
  z-index: 2;
  width: 100%;
}

.linksContainer {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-justify-content: flex-end;
          justify-content: flex-end;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
}

.linksContainer {
  margin-right: 3%;
  /* cursor: pointer; */
  font-size: 18px;
  text-decoration: none;
}

.links {
  text-decoration: none;
  cursor: pointer;

  color: inherit;
}

.links:hover {
  text-decoration: none;
  color: #b6b6b6;
}

.logoContainer {
  float: left;
  margin-left: 3%;
  cursor: pointer;
  height: 80px;
  width: 80px
}


@media only screen and (max-width: 768px) {
  .linksContainer {
    font-size: 11px;
  }
  .logoContainer{
    height: 50px;
    width: 50px
  }
}
.App {
  font-family: "Poppins", sans-serif;
  scroll-behavior: smooth;

}

.particleContainer {
  height: 100vh;
  background-color: #271e52;
}

.heroTextContainer {
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
}

.heroTitleText {
  font-size: 70px;
  font-weight: bold;
  line-height: 50px;
  color: #ffffff;
  text-align: center;
  margin-bottom: 10px;
}

.heroSubText {
  font-size: 20px;
  font-weight: 400;
  line-height: 50px;
  color: #ffffff;
  text-align: center;
}

@media only screen and (max-width: 768px) {
  .heroTitleText {
    font-size: 50px;
  }
}

.teamTitle {
  font-size: 30px;
  font-weight: 600;
  line-height: 50px;
  color: #3f4451;
  text-align: center;
}

.teamFlexContainer {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-flow: wrap;
          flex-flow: wrap;
  -webkit-justify-content: space-between;
          justify-content: space-between;
}

.teamImages {
  /* height: 500px; */
  cursor: pointer;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-flex: 0 0 50%;
          flex: 0 0 50%;
  height: 35rem;
  -webkit-justify-content: flex-end;
          justify-content: flex-end;
  -webkit-flex-direction: column;
          flex-direction: column;
  background-image: url("https://sscrypts.com/images/3.jpg");

  /* background-image: url("https://spyderscrypt.github.io/sscryptwebsite/3.jpg"); */
  background-size: 100%; /** moved this property down */
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;

  will-change: background;
  transition: background-image 0.2s ease-in-out;
}

.teamImages:hover {
  cursor: pointer;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-flex: 0 0 50%;
          flex: 0 0 50%;
  height: 35rem;
  -webkit-justify-content: flex-end;
          justify-content: flex-end;
  -webkit-flex-direction: column;
          flex-direction: column;
  background-image: url("https://sscrypts.com/images/4.jpg");

  /* background-image: url("https://spyderscrypt.github.io/sscryptwebsite/4.jpg"); */
  background-size: 100%; /** moved this property down */
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
}

.teamDescription {
  color: #ffffff;
  font-size: 1.5rem;
  font-weight: 500;
  margin-top: 0px;
  margin-bottom: 0px;
  text-decoration: none;
  position: relative;
  -webkit-align-self: flex-start;
          align-self: flex-start;
  bottom: 40%;
  left: 5%;
}

@media only screen and (max-width: 768px) {
  /* For desktop: */
  .teamDescription {
    font-size: 1rem;
  }
}

.instagramFeedPageContainer {
  margin-top: 5%;
  margin-bottom: 5%;
}

.instagramPostContainer {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-around;
          justify-content: space-around;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
}

.instagramTitle {
  font-size: 30px;
  font-weight: 600;
  line-height: 50px;
  color: #3f4451;
  text-align: center;
  margin-bottom: 5%;
}

.footerContainer {
  background-color: #222222;
  display: -webkit-flex;
  display: flex;
  padding: 5% 5% 5% 5%;
  color: #ffffff;
}

.contactContainer {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: space-around;
          justify-content: space-around;
  -webkit-flex-basis: 100%;
          flex-basis: 100%;
}

.contactField {
  display: -webkit-flex;
  display: flex;
}

.contactText {
  margin: 0;
  -webkit-align-self: center;
          align-self: center;
  margin-left: 10px;
}

.instagramLink {
  text-decoration: none;
  color: inherit;
}



@media only screen and (max-width: 768px) {
  .footerContainer {
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
  }
}

